// Specification: Metrics section aggregating MetricCard components.
// Computes key metrics from job applications and renders them in a responsive grid.

import { JobApplication } from '../../types/jobApplication';
import { parseDateOnlyUTC } from '../../utils/dateOnly';
import { MetricCard } from './MetricCard';

type MetricsSectionProps = {
  applications: JobApplication[];
};

export function MetricsSection({ applications }: MetricsSectionProps) {
  const total = applications.length;

  const nowMs = Date.now();
  const sevenDaysAgoMs = nowMs - 7 * 24 * 60 * 60 * 1000;
  const last7Days = applications.filter(
    (app) => parseDateOnlyUTC(app.applicationDate).getTime() >= sevenDaysAgoMs,
  ).length;

  const todayKey = new Date().toISOString().slice(0, 10);
  const todayCount = applications.filter(
    (app) => parseDateOnlyUTC(app.applicationDate).toISOString().slice(0, 10) === todayKey,
  ).length;

  return (
    <div className="grid gap-4 md:grid-cols-3">
      <MetricCard title="Total postulaciones" value={total} hint="Todo tu esfuerzo acumulado" />
      <MetricCard title="Ultimos 7 dias" value={last7Days} hint="Actividad reciente" />
      <MetricCard title="Hoy" value={todayCount} hint="Postulaciones en la fecha de hoy" />
    </div>
  );
}
